import { Link } from 'react-router-dom'
import '../myPetPage.css'

const PetCard = (props) => {
    const getPets = () => {
        return props.pets.map((pet, index) => {
            return (
                <div className='pet-card' key={index}>
                    <div className='pet-pic-container'>
                    <img className="pet-picture" src="data:image/jpeg;base64," alt="pet"/>
                    </div>
                    <h3>{pet.name}</h3>
                    <span>{pet.status}</span>
                    <Link to={`/pet/${pet._id}`}>See more</Link>
                </div>
            )
        }).sort((a,b)=>b-a)
    }
    return getPets()
}

export default PetCard