import axios from 'axios';
import { useEffect, useState } from 'react';
import { Button, Table } from 'react-bootstrap';
import '../petPage.css'


const Pet = (props) => {

    const [petInfo, setPetInfo] = useState(null)
    const [isLogged, setIsLogged] = useState(false)
    const [isOwned, setIsOwned] = useState(null)
    const [isSaved, setIsSaved] = useState(false)

    const currentPath = window.location.pathname.slice(5)

    const adopt = () => {
        const response = {status: 'Adopted', userId: localStorage.getItem('id')}
        axios.post(`https://yoyo-pet-adoption.herokuapp.com/pet/${currentPath}/adopt`, response)
        .then(res => {
            setIsOwned(localStorage.getItem('id'))
            console.log(res)
        })
        .catch(err => console.error(err.response.data))
    }
    const foster = () => {
        const response = {status: 'Fostered', userId: localStorage.getItem('id')}
        axios.post(`https://yoyo-pet-adoption.herokuapp.com/pet/${currentPath}/foster`, response)
        .then(res => {
            setIsOwned(localStorage.getItem('id'))
            console.log(res)
        })
        .catch(err => console.error(err.response.data))
    }
    const returnPet = () => {
        const response = {userId: localStorage.getItem('id')}
        axios.post(`https://yoyo-pet-adoption.herokuapp.com/pet/${currentPath}/return`, response)
        .then(res => {
            console.log(res)
            setIsOwned('')
        })
        .catch(err => console.error(err.response.data))
    }

    const savePet =  () => {
        const response = {userId: localStorage.getItem('id')}
         axios.post(`https://yoyo-pet-adoption.herokuapp.com/pet/${currentPath}/save`, response)
        .then(res => {
            setIsSaved(true)
            console.log(res)
        })
    }

    const unsavePet = async () => {
        const response = {userId: localStorage.getItem('id')}
        await axios.post(`https://yoyo-pet-adoption.herokuapp.com/pet/${currentPath}/unsave`, response)
        .then(res => {
            setIsSaved(false)
            console.log(res.data)
        })
    }

    useEffect(()=>{
        console.log('effected')
            if(localStorage.getItem('id')) {
                setIsLogged(true)
            }
            axios.get(`https://yoyo-pet-adoption.herokuapp.com/pet/${currentPath}`)
            .then(res=>{
                setPetInfo(res.data[0])
                setIsOwned(res.data[0].owner)
            })
            axios.get(`https://yoyo-pet-adoption.herokuapp.com/user/${localStorage.getItem('id')}`, {headers : {"Authorization" : `Bearer ${localStorage.getItem('token')}`}})
            .then(res => {
                if(res.data.saved.some(e => e._id === currentPath)) {
                    setIsSaved(true)
                } else {
                    setIsSaved(false)
                }
            })
        }, [isOwned, currentPath])
    
    return (
    <div className='my-container'>
        {petInfo && <div className='pet-page-card'>
        {isSaved && <svg onClick={unsavePet} style={{cursor: "pointer", fill:"#ffeedc"}} xmlns="http://www.w3.org/2000/svg" viewBox="-2 -4 24 24" width="32" height="32" preserveAspectRatio="xMinYMin" className="icon__icon"><path d="M9.293 1.55l.707.708.707-.707a5 5 0 1 1 7.071 7.071l-7.07 7.071a1 1 0 0 1-1.415 0L2.222 8.622a5 5 0 1 1 7.07-7.071z"></path></svg>}
        {!isSaved && <svg onClick={savePet} style={{cursor: "pointer", fill:"#ffeedc"}} xmlns="http://www.w3.org/2000/svg" viewBox="-2 -4 24 24" width="32" height="32" preserveAspectRatio="xMinYMin" className="icon__icon"><path d="M3.636 7.208L10 13.572l6.364-6.364a3 3 0 1 0-4.243-4.243L10 5.086l-2.121-2.12a3 3 0 0 0-4.243 4.242zM9.293 1.55l.707.707.707-.707a5 5 0 1 1 7.071 7.071l-7.07 7.071a1 1 0 0 1-1.415 0l-7.071-7.07a5 5 0 1 1 7.07-7.071z"></path></svg>}
            <img className='pet-page-picture'src="data:image/jpeg;base64," alt="pet"/>
            <Table striped bordered className='infos-table'>
            <tbody>
                 <tr>
                    <td className='name-info'>Type :</td>
                    <td className='name-value'>{petInfo.type}</td>
                </tr>
                <tr>
                    <td className='name-info'> Name :</td>
                    <td className='name-value'>{petInfo.name}</td>
                </tr>
                <tr>
                    <td  className='name-info'>Status :</td>
                    <td className='name-value'>{petInfo.status}</td>
                </tr>
                <tr>
                    <td  className='name-info'>Height :</td>
                    <td className='name-value'>{petInfo.height}</td>
                </tr>
                <tr>
                    <td className='name-info'>Weight :</td>
                    <td className='name-value'>{petInfo.weight}</td>
                </tr>
                <tr>
                    <td className='name-info'>Color :</td>
                    <td className='name-value'>{petInfo.color}</td>
                </tr>
                <tr>
                    <td className='name-info'>Hypoallergenic :</td>
                    <td className='name-value'>{petInfo.hypoallergenic ? 'Yes' : 'No'}</td>
                </tr>
                <tr>
                    <td className='name-info'>Breed :</td>
                    <td className='name-value'>{petInfo.breed}</td>
                </tr>
            </tbody>
            </Table> 
            {(isLogged && petInfo.status === 'Available') && <div style={{margin: "10px", display: "flex", width:"100%", justifyContent: "space-around"}}> 
            <Button onClick={adopt} className='btn-dark  pet-page-button'> Adopt </Button>
            <Button onClick={foster} className='btn-dark pet-page-button'> Foster </Button>
            </div>}
            {(isLogged && petInfo.owner === localStorage.getItem('id')) && <div style={{margin: "10px", display: "flex", width:"100%", justifyContent: "space-around"}}> 
            <Button onClick={returnPet} className='btn-dark pet-page-button'> Return </Button>
            </div>}
        </div>}
    </div>
  );
};

export default Pet;
